      link: 'https://www.meetup.com/Projektmanagement-Meetup-Roundtable/events/269466550/',
      description: '<p>Agenda:</p> <p>18:00 Uhr – Begrüßung, Einleitung, Infos zum Ablauf</p> <p>18:15 Uhr – Vortrag 1: -&gt; „Office 365 –Professionelles Arbeiten im Homeoffice: Wie können die Office 365 und Edison 365 Apps ideal für das Arbeiten im Homeoffice genutzt werden? – Digitaler Arbeitsplatz der Zukunft in Corona Zeiten</p> <p>Vortrag Herr Andreas Hock, Partner 3Pworx GmbH</p> <p>\'Aufgrund der derzeitigen Corona Krise muss rasch ein digitaler Arbeitsplatz geschaffen werden – und zwar im Homeoffice, der allen Anforderungen genügt\'</p> <p>Millionen von Personen in Deutschland wurden zum Arbeiten ins Homeoffice geschickt. Wie kann der Spagat gelingen?<br/>Eine der führenden professionellen Lösungen, um effizient und professionell vom Homeoffice aus arbeitsfähig zu sein ist Office 365.<br/>Office 365 bietet hier vor allem interessante Lösungen und Apps aus dem Bereich der digitalen Zusammenarbeit, wie MS Teams, MS Planner, MS Power BI.</p> <p>Agile Methoden für Projekte werden ebenfalls verstärkt unterstützt.<br/><a href=\'https://3pworx.com/projektmanagement-mit-office-365/\' class=\'linkified\'>https://3pworx.com/projektmanagement-mit-office-365/</a></p> <p>Dieses Paket kann im Homeoffice durch Lösungen aus dem Bereich Ideenmanagement, Business Cases und Projekt-Kollaboration mit den fertigen Edison 365 Produktlösungen intelligent und nachhaltig erweitert werden.</p> <p>Die nahtlose Integration ins Office 365 Umfeld ist sehr einfach darstellbar. Weitere Schnittstellen zu MS Teams, usw. sind schlüsselfertig.</p> <p><a href=\'https://3pworx.com/edison-365/\' class=\'linkified\'>https://3pworx.com/edison-365/</a></p> <p>In diesem Webinar schildern wie Ihnen einige Anwendungsfälle, wie Sie schnell Office 365 ideal fürs Homeoffice nutzen können uns wir geben Ihnen einen interessanten Einblick der Erweiterung mit der Edison 365 Toolsuite, um hier einen zusätzlichen Mehrwert zu erhalten.</p> <p>19:15 Uhr – Vortrag 2: „Erfolgreich handeln in der Corona Krise – Funktionierende Managementprinzipien</p> <p>Vortrag von Herrn Prof. Dr. Komus</p> <p>Die aktuelle Corona-Krise schüttelt Wirtschaft, Management und Privatleben grundlegend durcheinander. Gute Entscheidungen sind schwierig – Sicherheit ist nicht zu haben.</p> <p>Trotzdem oder besser gerade deswegen gilt es, zu reflektieren, welche Entscheidungsmuster und Managementprinzipien in welchem Kontext funktionieren.</p> <p>Um die aktuellen Herausforderungen zu managen reichen selbst agile Methoden wie Scrum so wie sie meist gelebt werden nicht aus.</p> <p>Handlungsmuster für das komplex-chaotische Umfeld wie „MaxAgil“ (die pragmatische Maximierung agiler Prinzipien) oder „Effectuation“ versprechen bessere Ergebnisse. Gleichwohl gilt es auch in der Corona-Krise differenziert sinnvolle Lösungsansätze zu entwickeln. Der Beitrag zeigt wie das funktionieren kann und welche Schritte zu gehen sind.<br/>Prof. Dr. Ayelt Komus ist seit langem Experten und Vordenker für erfolgreiches Management bei Unsicherheit. Es hat früh die Potenziale und Anwendungsfelder und Grenzen agiler Methoden aufgezeigt. Seine Schwerpunkte sind agile und hybride Methoden auf Ebene von Projektportfolio, Programm und Unternehmen (Scaled Agile).</p> <p>20:15 Uhr – Vortrag 3: „Hybrides Projektmanagement – Vorstellung der Studienergebnisse</p> <p>Vortrag von Herrn Gregor Diem</p> <p>Seit mehr als einem Jahr forscht Gregor Diem an der Edinburgh Business School, Schottland, UK, an dem Thema hybrides Projektmanagement.</p> <p>Ziel dieser Studie ist es, ein besseres Verständnis des hybriden Projektmanagements zu erlangen und den jüngsten Trend in dieser Disziplin zu untersuchen, bei dem agile und traditionelle Projektmanagementpraktiken kombiniert werden. Daher ist auch das Ziel dieser Studie mehr über diesen Trend und wie dieser in der Praxis angewendet wird zu erfahren.</p> <p>Die Umfrage kann über folgenden Link aufgerufen werden: <a href=\'https://hwsml.eu.qualtrics.com/jfe/form/SV_2tyWt5wjIOYDBTD\' class=\'linkified\'>https://hwsml.eu.qualtrics.com/jfe/form/SV_2tyWt5wjIOYDBTD</a></p> <p>Erste Ergebnisse werden im agilen Trendforum gigital am Do. [masked] vorgestellt. Über eine rege Umfrageteilnahme und anschließender Diskussion im agilen Trendforum würden wir uns sehr freuen.</p> <p>21:30 Uhr: Q&amp;A</p> <p>Ca. 22:00 Uhr - Ende des 3Pworx 3. Agilen Trendforums Juni 2020 digital</p> ',
